import type { ContextType, Ref, JSX } from 'preact';
import { useLayoutEffect, useRef } from 'preact/hooks';

import type { UnpackSignals } from '@oracle/oraclejet-internal-utilities/attributeUtils';

import { useAccessibleContext } from '#hooks/UNSAFE_useAccessibleContext';
import { useComponentTheme } from '#hooks/UNSAFE_useComponentTheme';
import { useFormFieldContext } from '#hooks/UNSAFE_useFormFieldContext';
import type { FormVariantContext } from '#hooks/UNSAFE_useFormVariantContext';
import { useTabbableMode } from '#hooks/UNSAFE_useTabbableMode';
import { useTextFieldInputHandlers } from '#hooks/UNSAFE_useTextFieldInputHandlers';
import { useTranslationBundle } from '#hooks/UNSAFE_useTranslationBundle';
import type { BundleType } from '#resources/nls/bundle';
import { isEmbeddedVariant } from '#utils/PRIVATE_formControlUtils';
import { classNames } from '#utils/UNSAFE_classNames';
import { type TextProps, textInterpolations } from '#utils/UNSAFE_interpolations/text';
import { mergeInterpolations } from '#utils/UNSAFE_mergeInterpolations';
import { mergeProps } from '#utils/UNSAFE_mergeProps';
import type { ValueUpdateDetail } from '#utils/UNSAFE_valueUpdateDetail';
import { isInputPlaceholderShown } from './TextFieldUtils';
import { formControlInheritTextAlign } from './themes/FormControlUtilsStyles.css';
import { TextFieldInputRedwoodTheme } from './themes/redwood/TextFieldInputTheme';
import { type TextFieldInputVariantOptions } from './themes/TextFieldInputStyles.css';

const interpolations = [...Object.values(textInterpolations)];

const styleInterpolations = mergeInterpolations<Props>(interpolations);

// The WHATWG forms spec specifies the following input types that support setSelectedRange
const supportsSetSelection = ['text', 'search', 'url', 'tel', 'password', 'textarea'];

type Autocomplete = 'off' | 'on' | string;

type OrigPickedPropsFromInput = Pick<
  UnpackSignals<JSX.IntrinsicElements['input']>,
  'autofocus' | 'id' | 'placeholder' | 'required' | 'role' | 'spellcheck' | 'type'
>;
type RenamedPropsFromInput = Omit<
  OrigPickedPropsFromInput,
  'autofocus' | 'readonly' | 'required'
> & {
  autoFocus?: OrigPickedPropsFromInput['autofocus'];
  isRequired?: OrigPickedPropsFromInput['required'];
};
type InputProps = {
  as?: 'input';
  type?: Pick<UnpackSignals<JSX.IntrinsicElements['input']>, 'type'>['type'];
  rows?: never;
  inputRef?: Ref<HTMLInputElement>;
  hasPrefix?: boolean;
  hasSuffix?: boolean;
};

type TextAreaProps = {
  as: 'textarea';
  type?: never;
  rows?: number;
  inputRef?: Ref<HTMLTextAreaElement>;
  hasPrefix?: never;
  hasSuffix?: never;
};

type FocusProps = Pick<UnpackSignals<JSX.HTMLAttributes<HTMLElement>>, 'onBlur' | 'onFocus'>;

type UniqueProps = InputProps | TextAreaProps;

type AriaProps = Pick<
  UnpackSignals<JSX.AriaAttributes>,
  | 'aria-autocomplete'
  | 'aria-controls'
  | 'aria-describedby'
  | 'aria-expanded'
  | 'aria-invalid'
  | 'aria-label'
  | 'aria-labelledby'
  | 'aria-valuemax'
  | 'aria-valuemin'
  | 'aria-valuenow'
  | 'aria-valuetext'
>;

type Props = UniqueProps &
  TextProps &
  RenamedPropsFromInput &
  AriaProps &
  FocusProps & {
    autoComplete?: Autocomplete;
    // TODO: Should we expose has* props like these or should we pass this info
    // via FormFieldContext?  (from review on 3/8/22)
    currentCommitValue?: string;
    hasEmptyLabel?: boolean;
    hasEndContent?: boolean;
    hasInsideLabel?: boolean;
    hasStartContent?: boolean;
    value?: string;
    variant?: ContextType<typeof FormVariantContext>;
    onInput?: (detail: ValueUpdateDetail<string>) => void;
    onCommit?: (detail: ValueUpdateDetail<string>) => void;
    onKeyDown?: (event: KeyboardEvent) => void;
    onKeyUp?: (event: KeyboardEvent) => void;
  };

/**
 * Merge multiple ids into a space-separated list
 * @param ids The ids to be merged
 * @returns The space separated string of ids
 */
function mergeIDs(...ids: (string | undefined)[]) {
  return ids.filter(Boolean).join(' ') || undefined;
}

export const TextFieldInput = ({
  as = 'input',
  'aria-autocomplete': ariaAutoComplete,
  'aria-controls': ariaControls,
  'aria-describedby': ariaDescribedBy,
  'aria-expanded': ariaExpanded,
  'aria-invalid': ariaInvalid,
  'aria-label': ariaLabel,
  'aria-labelledby': inputLabelledBy,
  'aria-valuemax': ariaValueMax,
  'aria-valuemin': ariaValueMin,
  'aria-valuenow': ariaValueNow,
  'aria-valuetext': ariaValueText,
  autoComplete,
  autoFocus,
  currentCommitValue,
  hasEmptyLabel,
  hasEndContent = false,
  hasInsideLabel = false,
  hasPrefix = false,
  hasStartContent = false,
  hasSuffix = false,
  id,
  inputRef,
  placeholder,
  isRequired,
  role,
  rows,
  spellcheck,
  type,
  value = '',
  variant = 'default',
  onInput,
  onCommit,
  onKeyDown,
  onKeyUp,
  onBlur,
  onFocus,
  ...props
}: Props) => {
  const { isDisabled, isFocused, isLoading, isReadonly } = useFormFieldContext();
  const { class: styleInterpolationClasses } = styleInterpolations(props);
  const hasValue = value !== '';
  const isTextArea = as === 'textarea';
  const isInput = as === 'input';
  const isPassword = type === 'password';
  const renderPrefix = !isDisabled && hasPrefix;
  const renderSuffix = !isDisabled && hasSuffix;
  const myPlaceholder = isInputPlaceholderShown(hasInsideLabel, hasValue, isFocused)
    ? placeholder
    : undefined;

  const { classes } = useComponentTheme<TextFieldInputVariantOptions>(TextFieldInputRedwoodTheme, {
    type: isPassword ? 'isPassword' : 'notPassword',
    styleVariant: isEmbeddedVariant(variant) ? 'embedded' : 'default',
    textarea: isTextArea ? 'isTextArea' : 'notTextArea',
    input: isInput ? 'isInput' : 'notInput',
    div: 'notDiv',
    prefix: renderPrefix ? 'hasPrefix' : 'noPrefix',
    suffix: renderSuffix ? 'hasSuffix' : 'noSuffix',
    startContent: hasStartContent ? 'hasStartContent' : 'noStartContent',
    endContent: hasEndContent ? 'hasEndContent' : 'noEndContent',
    insideLabel: hasInsideLabel ? 'hasInsideLabel' : 'noInsideLabel',
    value: hasValue ? 'hasValue' : 'noValue',
    focused: isFocused ? 'isFocused' : 'notFocused',
    disabled: isDisabled ? 'isDisabled' : 'notDisabled'
  });

  const inputClasses = classNames([
    classes,
    styleInterpolationClasses,
    // JET-72775 - Form control alignment in tables
    // When we are in legacy collection components, we want to inherit the text align
    // when it is not set explicitly.
    variant === 'legacyEmbedded' && props.textAlign === undefined && formControlInheritTextAlign
  ]);
  const Comp = as || 'input';

  // ref to hold the text selection to restore.
  const selectionRef = useRef({
    start: 0,
    end: 0
  });

  const textFieldInputHandlers = useTextFieldInputHandlers({
    currentCommitValue,
    // Enter should not commit for textarea.
    isCommitOnEnter: as === 'input',
    value,
    onInput,
    onCommit,
    onKeyDown,
    selectionRef
  });
  const handlers = mergeProps(textFieldInputHandlers, { onBlur, onFocus });
  // the implicit default for tabindex on an input is 0, so do not explicitly set it.
  // JET-52914 - Implement Tabbable mode API contract in InputText
  // if not tabbable, spread tabbableModeProps on component
  const { isTabbable, tabbableModeProps } = useTabbableMode();
  // TODO: for autoComplete='off', need to configure attrs appropriately to make sure it
  // works across browsers and versions  (from review on 3/11/22)

  // JET-52089: add unsafe API for specifying an external label which is added to ariaLabelledBy on the input,
  // only when labelEdge is 'none' and label is ''. Since ariaLabelledBy takes precedence over all other
  // kinds of labels, this helps to ensure we don't override a meaningful label.
  const { UNSAFE_ariaLabelledBy } = useAccessibleContext();
  const ariaLabelledBy = hasEmptyLabel
    ? mergeIDs(inputLabelledBy, UNSAFE_ariaLabelledBy)
    : inputLabelledBy;

  const translations = useTranslationBundle<BundleType>('@oracle/oraclejet-preact');
  const loadingAriaLabel = translations.formControl_loading();

  // Restore the saved selection when the value changes.  See JET-62763 for info on why this is needed.
  useLayoutEffect(() => {
    // This should never be a function in this case, This is here because of the Ref type.
    if (typeof inputRef !== 'function') {
      const inpElem = inputRef?.current;
      // only set the selection range if the element has focus
      // only set the selection range on supported types.
      if (
        document.activeElement === inpElem &&
        inpElem &&
        supportsSetSelection.includes(inpElem.type)
      ) {
        inpElem.setSelectionRange(selectionRef.current.start, selectionRef.current.end);
      }
    }
  }, [value, inputRef]);

  return (
    <Comp
      aria-autocomplete={ariaAutoComplete}
      aria-controls={ariaControls}
      aria-describedby={ariaDescribedBy}
      aria-expanded={ariaExpanded}
      aria-invalid={ariaInvalid}
      aria-label={isLoading ? loadingAriaLabel : ariaLabel ? ariaLabel : undefined}
      aria-labelledby={ariaLabelledBy}
      aria-required={isRequired ? true : undefined}
      aria-valuemax={ariaValueMax}
      aria-valuemin={ariaValueMin}
      aria-valuenow={ariaValueNow}
      aria-valuetext={ariaValueText}
      autocomplete={autoComplete}
      autofocus={autoFocus}
      class={inputClasses}
      disabled={isDisabled}
      id={id}
      onKeyUp={onKeyUp}
      placeholder={myPlaceholder}
      readonly={isReadonly}
      // @ts-expect-error TS cannot infer the type correctly for the polymorphed ref
      ref={inputRef}
      role={role}
      rows={rows}
      spellcheck={spellcheck}
      type={type}
      value={value}
      {...handlers}
      {...(!isTabbable && tabbableModeProps)}
    />
  );
};
